@mixin item-shift ($gap) {
  margin-left: $item-gap;
  margin-right: $item-gap;
}

@mixin star-size ($size) {
  width: $star-size;
  height: $star-size;
}

@mixin font-size ($size, $weight) {
  font-size: $size;
  font-weight: $weight;
}

@mixin logo-size ($size) {
  height: $logo-size;
  width: $logo-size;
}
